.App {
   display: flex;
   flex-direction: row;
   height: 100vh;
   justify-content: center;
   align-content: center;
   background-color: yellow;
}

.Game {
}

.Board {
   display: flex;
   flex-direction: row;
   background-color: rgb(35, 35, 35);
   padding: 20px;
   border-radius: 40px;
}

.Column {
   display: flex;
   flex-direction: column-reverse;
}

.Slot {
  width: 80px;
  height: 80px;
  border-radius: 40px;
  margin: 10px;
}

@media screen and (max-width: 600px) {
   .Slot {
      width: 30px;
      height: 30px;
      border-radius: 15px;
      margin: 4px;
   }
}
